@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap);
.navbar {
    width: 100%;
    padding: .5rem 1rem;

    text-align: center;
}

.main-nav {
    margin: 0 1rem;
    transition: text-shadow .2s;

    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    text-decoration: none;
    
    color: #EBF5FF !important;
}

.main-nav:hover {
    text-shadow: 0px 0px 20px #CED8E2;
}

.main-nav.active {
    text-shadow: 0px 0px 20px #CED8E2;
    font-style: italic;
}
.footer {
    position: absolute;
    width: 100%;
    padding: 1rem 0;
    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;

    background: linear-gradient(180deg, #000A14 0%, #1E2832 100%);
    color: #CED8E2;
}

.col {
    align-items: center;
    align-content: center;
}

.col-logo {
    display: block;
    justify-content: right;
    text-align: end;
    margin-right: 1rem;
}

.footer-logo {
    max-height: 150px;
}

.col-contact {
    padding-top: 2rem;
}

.footer-icon {
    margin-right: 1rem;

    color: #B1BBC5;
}

.footer-icon:hover {
    box-shadow: 0px 0px 20px #CED8E2;
}

@media only screen and (max-width: 640px) {
    .footer {
        font-size: 16px;
    }
}
.home {
    text-align: center;

    background: linear-gradient(180deg, #3B454F 0%, #000A14 90%);
}

img {
    padding: 2% 0;
}

.text-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.greeting-container {
    position: absolute;
    top: 50%;
    height: 18vw;
    overflow: hidden;

    background-color: rgba(30, 40, 50, .5);

    padding: 0px 20px;
}

.greeting {
    display: block;
    animation: spin_words 5s infinite;
    margin: 2px 2px;

    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 12vw;

    color: #EBF5FF;
    text-shadow: 0px 0px 8px #EBF5FF;
}

@media only screen and (max-height: 800px) {
    img {
        max-height: 800px;
    }
}

@keyframes spin_words {
    10% {
        transform: translateY(-112%);
    }
    25% {
        transform: translateY(-100%);
    }
    35% {
        transform: translateY(-212%);
    }
    50% {
        transform: translateY(-200%);
    }
    60% {
        transform: translateY(-312%);
    }
    75% {
        transform: translateY(-300%);
    }
    85% {
        transform: translateY(-412%);
    }
    100% {
        transform: translateY(400%);
    }
}
html {
    background: #1E2832;
}

.page {    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;

    text-shadow: 0px 0px 20px #000A14;

    background: #000A14;
    color: #EBF5FF;
}

.page p {
    font-family: Quicksand;
    font-style: normal;
    font-weight: 500;
}

a:hover {
    cursor: pointer;
}

.carousel-caption {
    background: linear-gradient(
        90deg, 
        transparent, 
        #000A14, 
        transparent);
    
    transition: text-shadow .5s;
}

.carousel-img {
    border-radius: 100px;
    max-height: 80vh;

    transition: transform .5s;
}

.carousel-img:hover {
    transform: scale(1.1);
}

.carousel-img:hover + .carousel-caption {
    text-shadow: 0px 0px 30px #EBF5FF;
}

.modal {
    color: #000A14;
}

.modal-header{
    background: linear-gradient(90deg, #EBF5FF 20%, #1E2832 100%);
}

.modal-title {
    font-family: Cormorant Infant;
}

.modal-body {
    font-family: Quicksand;
    font-size: 18px;

    background: #EBF5FF;
}

@media only screen and (max-width: 991px) {
    .carousel-caption {
        background: rgb(0, 10, 20, .5);
    }
}
.card {
    margin: 2.5vw;
    border-color: #1E2832 !important;
    transition: transform .5s, box-shadow .5s;
}

.card:hover {
    text-shadow: 0px 0px 10px #EBF5FF;
    box-shadow: 0px 0px 2.5px 2.5px #B1BBC5;

    transform: scale(1.1);
    justify-content: center;
}

.card-img {
    padding: 0;
}

.card-img-overlay {
    background: linear-gradient(
        0deg,
        transparent 20%,
        #000A14 80%);
    opacity: .95;
    transition: opacity .2s;
        
    text-align: center;
}

.card-img-overlay:hover {
    opacity: 1;
}

.card-title, .card-text {
    color: #EBF5FF;
}

.source {
    margin-top: 10px;
}
